/**
 *
 * ConsolePageHeader
 *
 */

import React, { SFC } from 'react';
import { ButtonProps } from 'reactstrap';
import styled from 'styled-components';

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  margin-bottom: 2rem;
  flex-wrap: wrap;
`;

const SubHeaderWrapper = styled.div`
  flex-basis: 100%;
`;

interface ConsolePageHeaderProps {
  header: JSX.Element | string;
  actionButton?: React.SFC<ButtonProps>;
  subHeader?: JSX.Element;
}

const ConsolePageHeader: SFC<ConsolePageHeaderProps> = (props) => {
  return (
    <Wrapper>
      <h2>{props.header && props.header}</h2>
      <div className="ml-auto">
        {props.actionButton && <props.actionButton />}
      </div>
      {props.subHeader && (
        <SubHeaderWrapper> {props.subHeader}</SubHeaderWrapper>
      )}
    </Wrapper>
  );
};

export default ConsolePageHeader;



