/**
 *
 * ConsolePage
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';
import ZeroState, { ZeroStateProps } from 'components/ZeroState';
import LoadingIndicator from '../LoadingIndicator';

const ContentWrapper = styled.div`
  padding: 0 2rem;
`;

const BodyWrapper = styled<ConsolePageProps, 'div'>('div')`
  padding: 0 2rem;
  flex-grow: 1;
  height: 100%;
  overflow-y: ${({ allowScroll }) => (allowScroll ? 'auto' : '')};
  display: flex;
  padding-top: 0.1rem; /* add a tiny bit of padding so things in the body don't get cut off */
`;

interface ZeroStateConfig extends ZeroStateProps {
  show: boolean;
}

interface ConsolePageProps {
  subheader?: JSX.Element;
  zeroState?: ZeroStateConfig;
  isFetchingAssets?: boolean;
  allowScroll?: boolean;
}

const ConsolePage: SFC<ConsolePageProps> = (props) => {
  if (props.isFetchingAssets) {
    return <LoadingIndicator forPage />;
  } else if (props.zeroState.show) {
    return (
      <BodyWrapper allowScroll={props.allowScroll}>
        <ZeroState {...props.zeroState} />
      </BodyWrapper>
    );
  } else {
    return (
      <React.Fragment>
        {props.subheader && (
          <ContentWrapper> {props.subheader} </ContentWrapper>
        )}
        <BodyWrapper allowScroll={props.allowScroll}>
          {props.children}
        </BodyWrapper>
      </React.Fragment>
    );
  }
};

ConsolePage.defaultProps = {
  isFetchingAssets: false,
  allowScroll: true,
  zeroState: {
    show: false,
    title: null,
    info: null,
    addButton: null,
    icon: null,
  },
};

export default ConsolePage;



