/**
 *
 * ConsolePageFooter
 *
 */

import React, { SFC } from 'react';
import styled from 'styled-components';

const MyFoot = styled.div`
  min-height: 3.5rem;
  display: flex;
  align-items: center;
  padding: 0.5rem 1rem;
  z-index: 1;
  background: white;
`;

const ActionButtonContainer = styled.div`
  margin-left: auto;
`;

interface ConsolePageFooterProps {
  actionButtons?: React.SFC<{}>;
  leftButtons?: React.SFC<{}>;
}

const ConsolePageFooter: SFC<ConsolePageFooterProps> = (props) => {
  return (
    <MyFoot>
      {props.leftButtons && <props.leftButtons />}
      {props.actionButtons && (
        <ActionButtonContainer>
          <props.actionButtons />
        </ActionButtonContainer>
      )}
    </MyFoot>
  );
};

export default ConsolePageFooter;



