/**
 *
 * ZeroState
 *
 */

import React, { SFC } from 'react';
import { FormattedMessage } from 'react-intl';
import { Jumbotron } from 'reactstrap';
import styled from 'styled-components';

const JumboWrapper = styled(Jumbotron)`
  width: 100%;
  display: inline-table; /* note: this allows the jumbotron to only take up the space it needs, rather than the entire page */
`;

export interface ZeroStateProps {
  title: FormattedMessage.MessageDescriptor;
  icon: JSX.Element;
  info: FormattedMessage.MessageDescriptor;
  moreInfo?: JSX.Element;
  addButton: JSX.Element;
  className?: string;
}

const ZeroState: SFC<ZeroStateProps> = (props) => {
  return (
    <JumboWrapper className={props.className}>
      <h1 className="display-3">
        {props.icon}
        &nbsp;
        <FormattedMessage {...props.title} />
      </h1>
      <p className="lead">
        <FormattedMessage {...props.info} />
      </p>
      <hr className="my-2" />
      {props.moreInfo && <p>{props.moreInfo}</p>}
      <p className="lead">{props.addButton}</p>
    </JumboWrapper>
  );
};

export default ZeroState;



