/*
 *
 * LibraryDetailPage
 *
 */
import React from 'react';
import { connect } from 'react-redux';
import Helmet from 'react-helmet';
import { FormattedMessage, injectIntl } from 'react-intl';
import messages from './messages';
import * as ducks from 'containers/Console/ducks';
import ConsolePageFooter from 'components/ConsolePageFooter';
import CbCodeEditor, { CbEditorLanguages } from 'components/CbCodeEditor';
import CbButton from 'components/CbButton';
import { RouteComponentProps } from 'react-router';
import { Button } from 'reactstrap';
import { TrashIcon } from 'components/EntityIcons';
import PageHeader from 'components/ConsolePageHeader';
import ConsolePage from 'components/ConsolePage';
import {
  createShowBaseModal,
  createShowConfirmationModal,
} from 'containers/Console/actions/ui';
import {
  setModifiableLibrary,
  updateModifiableLibrary,
} from 'containers/Console/actions/modifiableEnitites';
import { selectModifiableItem } from 'containers/Console/selectors/modifiableEnitites';
import LibraryTile from 'components/LibraryTile';
// import styled from 'styled-components';
import { LibraryModel } from 'cb-utils/console-entity-models';
import { overallUrls } from 'cb-utils/getUrls';
import CbPrompt, { confirmCancelHash } from 'components/CbPrompt';
import { bindActionCreators } from 'redux';
import { CBDispatch } from 'cb-utils/typeUtils';
import { AppState } from 'reducers';
import { createStructuredSelector } from 'reselect';

interface LibraryDetailPageProps
  extends RouteComponentProps<{ libraryName: string }> {}

type MyProps = LibraryDetailPageProps & ReactIntl.InjectedIntlProps;
type MasterProps = MyProps & IStateToProps & IDispatchToProps;

export class LibraryDetailPage extends React.PureComponent<MasterProps, {}> {
  componentWillMount() {
    this.props.fetchLibrary();
    this.props.fetchLibraries();
  }

  handleCodeChange = (code: string) => {
    this.props.updateModifiableLibrary({
      name: this.props.match.params.libraryName,
      code,
    });
  };

  handleSave = () => {
    this.props.updateLibrary(this.props.modifiableLibrary);
  };

  render() {
    const { libraryName } = this.props.match.params;
    const library = this.props.modifiableLibrary;

    if (!library) return null;
    if (!this.props.library) return null;

    const blockLeave =
      this.props.modifiableLibrary.code !== this.props.library.code;

    const title = (
      <PageHeader
        header={<div>{libraryName}</div>}
        subHeader={<div>{this.props.library.description}</div>}
        actionButton={() => (
          <div className="text-right">
            {library &&
              library.dependencies.map((name: string) => (
                <LibraryTile key={name} name={name} />
              ))}
            <Button
              onClick={() =>
                this.props.createShowBaseModal({
                  component: 'AddLibraryModal',
                  props: {
                    assetClass: 'libraries',
                    name: libraryName,
                  },
                })
              }
            >
              <FormattedMessage {...messages.addLibrary} />
            </Button>
          </div>
        )}
      />
    );
    return (
      <React.Fragment>
        <ConsolePage subheader={title}>
          <CbPrompt
            when={blockLeave}
            message={this.props.intl.formatMessage(messages.confirmLeave)}
          />
          <Helmet
            title={`ClearBlade - ${libraryName || 'Library Detail'}`}
            meta={[
              {
                name: 'description',
                content: 'Description of libraryDetailPage',
              },
            ]}
          />
          <CbCodeEditor
            language={CbEditorLanguages.JAVASCRIPT}
            onSaveShortcut={this.handleSave}
            value={library.code}
            onChange={this.handleCodeChange}
          />
        </ConsolePage>
        <ConsolePageFooter
          leftButtons={() => (
            <Button
              color="link"
              data-cy="delete-library"
              onClick={() => this.props.deleteLibrary(libraryName)}
            >
              <TrashIcon />
              &nbsp; <FormattedMessage {...messages.deleteLibrary} />
            </Button>
          )}
          actionButtons={() => (
            <React.Fragment>
              <CbButton
                className="mr-3"
                onClick={() =>
                  this.props.history.push(
                    overallUrls.services({}, confirmCancelHash),
                  )
                }
              >
                <FormattedMessage {...messages.discardChanges} />
              </CbButton>
              <CbButton
                color="primary"
                className="mr-3"
                onClick={this.handleSave}
              >
                <FormattedMessage {...messages.save} />
              </CbButton>
            </React.Fragment>
          )}
        />
      </React.Fragment>
    );
  }
}

const mapStateToProps = (
  state: AppState,
  { match: { params: { libraryName } } }: LibraryDetailPageProps,
) =>
  createStructuredSelector({
    libraries: ducks.librarySelectors.entities,
    library: ducks.librarySelectors.item(libraryName),
    modifiableLibrary: selectModifiableItem<LibraryModel>(
      'libraries',
      libraryName,
    ),
  });

type IStateToProps = ReturnType<ReturnType<typeof mapStateToProps>>;

const mapDispatchToProps = (
  dispatch: CBDispatch,
  { match: { params: { libraryName } }, intl }: MyProps,
) =>
  bindActionCreators(
    {
      fetchLibraries: ducks.libraries.actions.fetchAll.request,
      fetchLibrary: () =>
        ducks.libraries.actions.fetchOne.request({ name: libraryName }),
      updateLibrary: ducks.libraries.actions.update.request,
      deleteLibrary: (name: string) =>
        dispatch(
          createShowConfirmationModal(
            intl.formatMessage(messages.confirmDelete, { name }),
            ducks.libraries.actions.delete.request(name),
          ),
        ),
      createShowBaseModal,
      setModifiableLibrary,
      updateModifiableLibrary,
    },
    dispatch,
  );

type IDispatchToProps = ReturnType<typeof mapDispatchToProps>;

export default injectIntl(
  connect(mapStateToProps, mapDispatchToProps)(LibraryDetailPage),
);



