/**
 *
 * CbPrompt
 *
 */

import React, { SFC } from 'react';
// import styled from 'styled-components';
import { Prompt, withRouter, RouteComponentProps } from 'react-router';

// added and checked for so confim cancel does not show after hitting 'discard changes'
export const confirmCancelHash = '#cancel';

interface CbPromptProps extends RouteComponentProps<{}> {
  message: string;
  when: boolean;
}

const CbPrompt: SFC<CbPromptProps> = ({ message, ...props }) => {
  return (
    <Prompt
      message={(location) => {
        if (props.location.pathname === location.pathname) {
          return true; // will be called for tab query string and want to ignore
        }
        // don't ask again if they have purposely canceled
        if (location.hash === confirmCancelHash) return true;
        return message;
      }}
      {...props}
    />
  );
};

export default withRouter(CbPrompt);



